export class HomeViewControl extends window.H.ui.Control {

    constructor (options) {
        super()
        this.setAlignment(options.position)
        this.map = options.map
        this.center = options.center
        this.zoom = options.zoom
    }
    renderInternal (el, doc) {
        el.innerHTML = `
            <div id="ctrl-homeview">
                <div class="H_btn H_el">
                    <svg class="H_icon" version="1.1" id="lui-icon-home-ondark-solid-large"
                        xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" x="0px" y="0px" viewBox="0 0 48 48"
                        enable-background="new 0 0 48 48" xml:space="preserve">
                    <g>
                        <g id="lui-icon-home-ondark-solid-large-bounding-box" opacity="0">
                            <path fill="rgba(15, 22, 33, 0.6)" d="M47,1v46H1V1H47 M48,0H0v48h48V0L48,0z"/>
                        </g>
                        <path fill="rgba(15, 22, 33, 0.6)" d="M24,1.3412c-0.5992,0-1.1984,0.2437-1.6584,0.732L1,25h7v21h12V34h8v12h12V25h7L25.6584,2.0732
                            C25.1984,1.5849,24.5992,1.3412,24,1.3412L24,1.3412z"/>
                    </g>
                    </svg>

                    </div>
            </div>
        `

        el.addEventListener ('click', this.setHomeView.bind(this), false)
        
        super.renderInternal(el, doc)
    }
    
    setHomeView () {
        this.map.setCenter(this.center)
        this.map.setZoom(this.zoom)
    }
}