import React from "react"

export const Modal = ({ modal, toggleModal, dontShowModal }) => {
    return (
        modal ? 
            <lui-modal open top bgcloseable responsiveoverlay responsiveoverlay>
                <span slot="title">Healthcare Resources</span>
                <b>Description</b>
                <br/>
                <br/>
                Web app were designed as an analytical tool that provides information about hospitals around the country. 
                It is useful source of information for everyone: people who may need some medical cure, can find which hospital is nearest and less loaded. 
                Ambulance driver can use navigation service to find the best way to particular hospital. 
                Hospital administration or local authorities can use this tool as a point in decision making when it Is connected with equipment distribution and supply.  

                <br/>
                <br/> 
                <b>Data Features</b>
                <br/>
                <br/> 
                For the pilot project we used data from <a target="_blank" href="https://www.intensivregister.de/#/intensivregister">DIVI</a>. 
                It provides almost real-time (5 min update cycle) data on hospital load and capacity: number of patients in total, number of beds (ordinary and ICU), number and availability of ventilation machines and ECMO units.
                
                <br/>
                <br/> 
                <b>Main functionality</b>
                <br/>
                <br/> 
              
                Web web app shows most important information for each hospital. 
                You can see each hospital point colored according to workload (red, yellow and green), quantity of ICU (divided on low\high care). 
                And of course, there are a lot of navigational features, that offers you the best routes (to hospital according workload, from one hospital to another, from equipment supplier to hospital etc.)

                <br/>
                <br/> 
                <b>Technical description of the product</b>
                <br/>
                <br/> 

                Application was implemented with HERE Data Hub to store and update information about hospitals in real time. As a data source we decided to use DIVI portal.
                Web-application we implemented using efficient set of tools including HERE JavaScript API. It’s possible to customize routes based on traffic information and type of vehicle. 
                <br/>
                <br/>
                To solve last mile problem, we implemented functionality of Waypoint API that helps to order up to 100 points. 
                And finally, great functionality depends on isochrones which HERE Location Suite also supports. 
                Governmental or private organizations can use value added services for workstream optimization. 
                Data contributors are nationwide government aggregators.
                <br/>
                <br/>
                The idea is applicable in various business areas, such as emergency monitoring. 
                An international supplier of medical equipment is already interested in the solution. 
                {
                    localStorage.getItem("modal") !== "false" ?
                        <lui-button slot="controls" secondary onClick={dontShowModal}>Don't show again</lui-button>
                    :
                    <></>
                    
                }
                <lui-button slot="controls"  onClick={toggleModal}>Ok</lui-button>
            </lui-modal>
         : 
         
        <div></div>
    )
}