import './style.css'

export class ServicesLabelControl extends window.H.ui.Control {

    constructor (options) {
        super()
        this.setAlignment(options.position)
    }
    renderInternal (el, doc) {
        el.innerHTML = `
        <div class="service-label">
            <a class="service-label-item" target="_blank" href="https://developer.here.com/documentation/maps/3.1.15.1/dev_guide/index.html">
                HERE JavaScript API 3.1
            </a>
            <a class="service-label-item" target="_blank" href="https://developer.here.com/documentation/studio/map_customization_suite_hlp/dev_guide/index.html">
                HERE Data Hub
            </a>
            <a class="service-label-item" target="_blank" href="https://developer.here.com/documentation/routing-api/dev_guide/index.html">
                HERE Routing API
            </a>
            <a class="service-label-item" target="_blank" href="https://developer.here.com/documentation/traffic/dev_guide/topics/incident-data.html">
                HERE Traffic API
            </a>
        </div>
        `
        super.renderInternal(el, doc)
    }
}