export class InfoControl extends window.H.ui.Control {

    constructor (options) {
        super()
        this.setAlignment(options.position)
        this.toggleModal = options.toggleModal
    }
    renderInternal (el, doc) {
        el.innerHTML = `
            <div id="ctrl-homeview">
                <div class="H_btn H_el">
                    <svg version="1.1" id="lui-icon-information-ondark-outline-large"
                        xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" x="0px" y="0px" viewBox="0 0 48 48"
                        enable-background="new 0 0 48 48" xml:space="preserve">
                    <g>
                        <g opacity="0">
                            <path fill="#48DAD0" d="M47,1v46H1V1H47 M48,0H0v48h48V0L48,0z"/>
                        </g>
                        <polyline fill="none" stroke="rgba(15, 22, 33, 0.6)" stroke-width="2" stroke-miterlimit="10" points="21.1583,20.5054 24.1583,20.4894 
                            24.238,35.4892 	"/>
                        
                            <line fill="none" stroke="rgba(15, 22, 33, 0.6)" stroke-width="4" stroke-miterlimit="10" x1="21.1211" y1="13.5055" x2="25.1211" y2="13.4842"/>
                        
                            <line fill="none" stroke="rgba(15, 22, 33, 0.6)" stroke-width="2" stroke-miterlimit="10" x1="30.7379" y1="35.4547" x2="17.7381" y2="35.5238"/>
                        <circle fill="none" stroke="#fff" stroke-width="2" stroke-miterlimit="10" cx="24" cy="24.4903" r="21.2132"/>
                    </g>
                    </svg>
                </div>
            </div>
        `

        el.addEventListener ('click', () => this.toggleModal())
        
        super.renderInternal(el, doc)
    }
    
    setHomeView () {
        this.map.setCenter(this.center)
        this.map.setZoom(this.zoom)
    }
}